import classes from "./profileInfo.module.scss";
import React from "react";
import PropTypes from 'prop-types';
import Spinner from "../../common/spinner";
import userPhoto from '../../../assets/img/avatar.png';
import ProfileStatusWithHooks from "../profileStatus/profileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto}) => {
    const onPhotoSelected = (e) => {
        e.preventDefault();
        if (e.target.files.length === 1) {
            const photo = e.target.files[0];
            savePhoto(photo);
        }
    };
    console.log(profile);
    if (!profile) {
        return <Spinner/>;
    }
    const {photos, aboutMe, lookingForAJob, lookingForAJobDescription, fullName, contacts} = profile;
    return (
        <>
            <div className={classes.description}>
                <div>
                    <img className={classes.profilePhoto} alt={'user photo'} src={photos.large || userPhoto}/>
                    {isOwner && <input onChange={onPhotoSelected} type="file"/> }
                    <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                </div>
                <div className={classes.descr}>
                    <div>FullName: {fullName}</div>
                    <div>About me:  {aboutMe}</div>
                    <div>{lookingForAJob ? lookingForAJobDescription : null}</div>
                    <ul>
                        <li>facebook: {contacts.facebook}</li>
                        <li>insta: {contacts.instagram}</li>
                        <li>twitter: {contacts.twitter}</li>
                        <li>youtube: {contacts.youtube}</li>
                        <li>github: {contacts.github}</li>
                        <li>website: {contacts.website}</li>
                        <li>mainlink: {contacts.mainlink}</li>
                    </ul>
                </div>
            </div>
        </>
    );
};

ProfileInfo.propTypes = {
    profile: PropTypes.object,
    photos: PropTypes.object,
    status: PropTypes.any,
    updateStatus: PropTypes.func,
    isOwner: PropTypes.bool,
    savePhoto: PropTypes.func
};

export default ProfileInfo;